import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import "./awards.css"
import JSONData from "../data/awards.json"
import Image from "../components/Image"
import Carousel, { CarouselItem } from "../components/Carousel"

const awards = () => {
  return (
    <Layout page="awards">
      <Seo title="Awards" />
      <section className="title-div-white">
        <strong className="title-awards">IT IS OUR PLEASURE TO HELP</strong>
      </section>
      <section className="section-awards">
        <div className="widthLimit-awards">
          <div className="columns">
            <div className="column is-half has-text-centered">
              <div className="container">
                <Carousel>
                  {JSONData.thankYouLetter.map(data => <CarouselItem><Image src={data.name} alt={data.name} className="images" /></CarouselItem>)}
                </Carousel>
                <div className="titleOfSlider">THANK YOU LETTERS</div>
              </div>
            </div>
            <div className="column has-text-centered">
              <div className="container">
                <Carousel>
                  {JSONData.certificates.map(data => <CarouselItem><Image src={data.name} alt={data.name} className="images" /></CarouselItem>)}
                </Carousel>
                <div className="titleOfSlider">CERTIFICATES</div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="title-div-white" />
    </Layout>
  )
}

export default awards
